@use "../global/breakpoint" as bp;

.p-error {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	&__bg {
		&__gif {
			display: block;
			margin: auto;
			width: 100%;
			@include bp.tab {
				width: 50%;
        max-width: 1280px;
			}
		}
	}
}
