.c-button {
	position: relative;
	display: inline-block;
	border: 2px solid #fff;
	color: #fff;
	text-align: center;
	text-decoration: none;
	outline: none;
	overflow: hidden;
	&:hover {
		background-color: rgba(255, 255, 255, 0.2);
		box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.6);
	}
	&--white {
		@extend .c-button;
		background-color: rgba(255, 255, 255, 0.5);
		&:hover {
			background-color: white;
			box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.7);
			color: black;
		}
	}

	&--breadcrumbs {
		position: relative;
		display: inline-block;
		border: 2px solid #fff;
		color: #fff;
		text-align: center;
		text-decoration: none;
		outline: none;
		overflow: hidden;
		background-color: white;
		// box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.7);
		color: black;
		pointer-events: none;
	}

	&--red {
		@extend .c-button;
		border-color: red;
		background: rgba(255, 0, 0, 0.5);
		&:hover {
			background-color: red;
			box-shadow: 0px 0px 15px rgba(255, 0, 0, 0.7);
		}
	}
}

.c-button span {
	display: inline-block;
	transform: translate3d(0, -10px, 0);
	transition: transform 0.4s, opacity 0.4s;
	transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
	opacity: 0;
}

.c-button::before {
	position: absolute;
	display: inline-block;
	width: 100%;
	top: 50%;
	left: 0;
	font-weight: 700;
	letter-spacing: 0.12em;
	content: attr(data-hover);
	transform: translateY(-45%);
	transition: transform 0.4s, opacity 0.4s;
	transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
	box-sizing: border-box;
}
.c-button:hover::before {
	transform: translate3d(0, 50%, 0);
	opacity: 0;
}
.c-button:hover span {
	transform: translate3d(0, 2px, 0);
	opacity: 1;
}
.c-button:hover span:nth-child(1) {
	transition-delay: 0.08s;
}
.c-button:hover span:nth-child(2) {
	transition-delay: 0.16s;
}
.c-button:hover span:nth-child(3) {
	transition-delay: 0.24s;
}
.c-button:hover span:nth-child(4) {
	transition-delay: 0.32s;
}
.c-button:hover span:nth-child(5) {
	transition-delay: 0.4s;
}
.c-button:hover span:nth-child(6) {
	transition-delay: 0.48s;
}
.c-button:hover span:nth-child(7) {
	transition-delay: 0.56s;
}
.c-button:hover span:nth-child(8) {
	transition-delay: 0.64s;
}
